import { Outlet } from "react-router-dom";

import { useGlobal } from "Contexts/App";
import { BusinessProvider, SettingsProvider } from "Contexts/Business";
import {
  Wrapper,
  WrapperSX,
} from "Containers/BusinessSettings/LayoutComponents";
import Sidebar from "Containers/BusinessSettings/Sidebar";

const BusinessSettingsLayout = () => {
  const { isTablet } = useGlobal();

  return (
    <BusinessProvider>
      <SettingsProvider>
        <Wrapper sx={WrapperSX}>
          {isTablet && <Sidebar />}
          <Outlet />
        </Wrapper>
      </SettingsProvider>
    </BusinessProvider>
  );
};

export default BusinessSettingsLayout;
