import { Box } from "@mui/material";

import Sidebar from "Components/BusinessSettings/Sidebar";
// import useRenderCount from "Hooks/useRenderCount";

const SidebarContainer = () => {
  // useRenderCount("BusinessSettings -> Sidebar");
  return (
    <Box
      component="div"
      bgcolor="#ffffff"
      flex="0 1 auto"
      height="100%"
      width={342}
    >
      <Sidebar />
    </Box>
  );
};

export default SidebarContainer;
